body {
  font-family: PFHandbookPro, sans-serif;
  font-size: 22px;
  font-weight: 300;
  letter-spacing: 0em;
  color: #333333;
  min-height: 3000px;
}

.wrapper {
  width: 100%;
  min-height: 100%;
  overflow: hidden;
}

.container {
  max-width: 1500px;
  width: 100%;
  margin: 0 auto;
  padding: 0 10px;
}

.button {
  border-radius: 3px;
  padding: 18px 42px 18px 42px;
  background-color: #EF4D9C;
  color: #FFF;
  font-weight: 500;
  font-size: 16px;
  line-height: 24px;
  text-transform: uppercase;
}

.title {
  font-weight: 500;
  font-size: 56px;
  line-height: 64px;
  letter-spacing: 0.01em;
  text-transform: uppercase;
}

.header {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 120px;
  background: rgba(255, 255, 255, 0.82);
  backdrop-filter: blur(5px);
  border-bottom: 1px solid #E0E0E0;
}

.header__container {
  display: flex;
  justify-content: space-between;
  align-items: center;
  height: 100%;
}

.header__phone {
  font-size: 24px;
  line-height: 32px;
}

.navigation__list {
  display: flex;
  gap: 40px;
}

.navigation__link {
  font-size: 18px;
  line-height: 32px;
  text-transform: uppercase;
}

.main__mainslider {
  margin-bottom: 136px;
}

.main__about {
  margin-bottom: 136px;
}

.mainslider {
  height: 884px;
  background-color: #F8F8F8;
}

.about {
  border: 1px solid #000;
}

.about__title {
  margin-bottom: 48px;
}

.about__body {
  display: flex;
  justify-content: space-between;
}

.about__left-column {
  flex: 0 0 35%;
}

.about__line {
  width: 42px;
  height: 70px;
  border-right: 1px dashed #565658;
  margin: 8px 0;
}

.about__right-column {
  flex: 0 0 57%;
}

.about__description {
  margin-bottom: 40px;
}

.about__description_l {
  font-size: 28px;
  line-height: 36px;
  margin-bottom: 40px;
}

.about__description_s {
  font-size: 22px;
  line-height: 30px;
  margin-bottom: 30px;
}

.about__description_s:last-child {
  margin-bottom: 0;
}

.dignity {
  display: flex;
  align-items: center;
}

.dignity__image {
  width: 84px;
  margin-right: 44px;
}

.dignity__title {
  flex: 0 1 70%;
  font-weight: 500;
  font-size: 32px;
  line-height: 32px;
}